/** The search inputs  */
.search-box {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.search-box__input {
    font-size: 30pt;
    width: 25ch;
    border: solid rgb(138, 138, 138) 2px;
    border-radius: 1000px;
    padding: 5px 20px;
}

@media (max-width: 720px) {
    .search-box__input {
        padding: 10px 20px;
        width: 80vw;
        font-size: 20pt
    }
}

.search-box__logo {
    width: 500px;
    height: 150px;
}

@media (max-width: 720px) {
    .search-box__logo {
        width: 75%;
        height: 100px;
    }
}

.search-box__input:focus {
    outline: none;
}

.search-result {
    --current-radius: 20px;
    text-decoration: none;
  
    color: white;
    display: flex;
    flex-direction: row;
    align-items: center;
    text-align: center;
    min-height: 150px;

    background-repeat: no-repeat;
    background-blend-mode: multiply;
    background-position-x: 100px;
    background-position-y: 30%;
    background-size: 95%;
    -ms-interpolation-mode: nearest-neighbor;
        image-rendering: -moz-crisp-edges;
        image-rendering: pixelated;
  
    border-radius: var(--current-radius);
    background-color: var(--layer-1);
    transition: 0.10s all;
    -webkit-animation: emphasis 0.25s ease forwards;
            animation: emphasis 0.25s ease forwards;
    box-shadow: 2px 2px 0px var(--shadow-color);
}
    
.search-result:hover {
    background-color: var(--search-result-hover);
    /* outline: solid rgb(189, 53, 19) 3px; */
    cursor: pointer;
}

.search-result:active {
    -webkit-filter: brightness(90%);
            filter: brightness(90%);
    outline: none;
    box-shadow: 1px 1px 0px var(--shadow-color);
    /* border: solid transparent 3px; */
}

.search-result--disabled {
    pointer-events: none;
    -webkit-filter: brightness(50%);
            filter: brightness(50%);
    box-shadow: 1px 1px 0px var(--shadow-color);
}
.search-result--disabled:active {
    -webkit-filter: brightness(50%) !important;
            filter: brightness(50%) !important;
    background-color: var(--layer-1);
    box-shadow: 1px 1px 0px var(--shadow-color);
}
.search-result--disabled:hover {
    -webkit-filter: brightness(50%) !important;
            filter: brightness(50%) !important;
    cursor: default;
    background-color: var(--layer-1);
}

.search-result__image {
    width: 96px;
    height: 96px;
}

.search-result__img-container {
    background: var(--layer-2);
    display: grid;
    place-items: center;
    height: 100%;
    padding: 0 0.7em;
    border-radius: var(--current-radius) 0 0 var(--current-radius);
    box-shadow: 1px 0 1px var(--shadow-color);
}
/*  */
.search-result__info-container {
    display: grid;
    grid-template-rows: 1fr;
    grid-row: 50%;
    justify-content: center;
    align-content: center;
    width: 100%;
    height: 100%;
}

.search-result__type-container {
    display: flex;
    flex-direction: row;
    justify-content: center;
}

.search-result__type {
    display: inline-block;
    background-color: currentColor;
    text-shadow: 1px 1px 1px var(--shadow-color);
    font-weight: bold;
    border-radius: 10px;
    font-size: 10pt;
    padding: 5px;
    margin: 2px;
    width: 8ch;
}

.search-result__type--none {
    background-color: transparent;
    box-shadow: 0 0 5px inset white;
}

.search-result__name-container {
    padding: 0;
    margin: 0;
    display: flex;
    align-items: center;
    justify-content: center;
}

.search-result__name {
    margin: 0 1ch;
}

@-webkit-keyframes emphasis {
    0% {
        -webkit-transform: scale(0.95);
                transform: scale(0.95);
    }
    100% {
        -webkit-transform: scale(1);
                transform: scale(1);
    }
}

@keyframes emphasis {
    0% {
        -webkit-transform: scale(0.95);
                transform: scale(0.95);
    }
    100% {
        -webkit-transform: scale(1);
                transform: scale(1);
    }
}

