.copy-clicker {
    color: var(--text-color);
    padding: 5px;
    margin-top: 5px;
    display: inline-block;
    background-color: var(--layer-1);
    border: solid var(--layer-0) 1px;
    transition: all 0.25s ease; 
}

.copy-clicker:hover {
    cursor: pointer;
    background-color: var(--layer-0);
}

.copy-clicker:active {
    background-color: rgb(15, 20, 15);
    transition: color 0.05s ease;
}

.copy-clicker--copied::before {
    color: rgb(0, 192, 0);
    transition: 1s cubic-bezier(0.16, 1, 0.3, 1);
}

/** Tooltips and Tooltips and Tooltips and Tooltips and Tooltips and Tooltips */
.tooltip {
    position: relative;
    display: inline-block;
}

.tooltip::before {
    content: attr(data-hover-txt);
    position: absolute;
    -webkit-transform: translate(-50%, 220%);
            transform: translate(-50%, 220%);
    top: -10px;
    left: 50%; 
    width: auto;
    padding: 2px 10px;
    border-radius: var(--division-radius);
    background: var(--layer-3);
    box-shadow: 0 0 2px var(--shadow-color);
    color: var(--text-color);
    text-align: center;
    transition: all 0.25s;
    display: none;
    z-index: 999;
}

.tooltip:hover::before {
    display: block;
    -webkit-animation: fadein 0.25s ease;
            animation: fadein 0.25s ease;
}

.tooltip:hover::after {
    pointer-events: none;
    display: block;
    -webkit-animation: fadein 0.25s ease;
            animation: fadein 0.25s ease;
    content: '';
    position: absolute;
    width: 12px;
    height: 12px;
    left: 50%;
    -webkit-transform: translate(-50%,60%) rotate(45deg);
            transform: translate(-50%,60%) rotate(45deg);
    background-color: var(--layer-3);
}


/* Loading spinner by https://loading.io/css/ */

.lds-ring {
    display: inline-block;
    position: relative;
    margin: auto;
    width: 80px;
    height: 80px;
}
.lds-ring div {
    box-sizing: border-box;
    display: block;
    position: absolute;
    width: 64px;
    height: 64px;
    margin: 8px;
    border: 8px solid #fff;
    border-radius: 50%;
    -webkit-animation: lds-ring 1.2s cubic-bezier(0.5, 0, 0.5, 1) infinite;
            animation: lds-ring 1.2s cubic-bezier(0.5, 0, 0.5, 1) infinite;
    border-color: #fff transparent transparent transparent;
}
.lds-ring div:nth-child(1) {
    -webkit-animation-delay: -0.45s;
            animation-delay: -0.45s;
}
.lds-ring div:nth-child(2) {
    -webkit-animation-delay: -0.3s;
            animation-delay: -0.3s;
}
.lds-ring div:nth-child(3) {
    -webkit-animation-delay: -0.15s;
            animation-delay: -0.15s;
}
@-webkit-keyframes lds-ring {
    0% {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }
    100% {
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg);
    }
}
@keyframes lds-ring {
    0% {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }
    100% {
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg);
    }
}

