@-webkit-keyframes bounce {
    from {
        -webkit-transform: scale(0.95, 0.95);
                transform: scale(0.95, 0.95);
    }
    50% {
        -webkit-transform: scale(1.05, 1.05);
                transform: scale(1.05, 1.05);
    }
    to {
        -webkit-transform: scale(1, 1);
                transform: scale(1, 1);
    }
}

@keyframes bounce {
    from {
        -webkit-transform: scale(0.95, 0.95);
                transform: scale(0.95, 0.95);
    }
    50% {
        -webkit-transform: scale(1.05, 1.05);
                transform: scale(1.05, 1.05);
    }
    to {
        -webkit-transform: scale(1, 1);
                transform: scale(1, 1);
    }
}

@-webkit-keyframes fadein {
    0% {
        opacity: 0;
    }
    100% {
        opacity: 1;
    }
}

@keyframes fadein {
    0% {
        opacity: 0;
    }
    100% {
        opacity: 1;
    }
}

